<template>
    <footer class="footer">
        <textarea
            class="footer__edit"
            rows="1"
            placeholder="Написать сообщение..."
            v-model="message">
        </textarea>
        <button class="footer__btn" @click="window = true" v-if="message.length == 0">
            <img src="@/assets/imgs/Frame.svg" alt="">
        </button>
        <button class="footer__btn" @click="sendMess" v-else>
            <img src="@/assets/imgs/send.svg" alt="">
        </button>
        <div class="window" v-if="window" @click="window = false">
            <div class="window__body" @click.stop>
                <div class="window__title">Отправить картинку</div>
                <div class="window__from">
                    <label>
                        <span>URL</span>
                        <input v-model="photo">
                    </label>
                    <label>
                        <span>Комментарий</span>
                        <textarea v-model="comment"></textarea>
                    </label>
                </div>
                <div class="window__footer">
                    <button class="btn red" @click="window = false">Отмена</button>
                    <button class="btn purpure" @click="sendMessPhoto">Отправить</button>
                </div>
            </div>
        </div>
    </footer>
</template>

<script>
    export default {
        name: 'WindowFooter',
        props:{
            sendId: Number
        },
        data() {
            return {
                comment: '',
                photo: '',
                message: '',
                window: false,
                hours: new Date().getHours() < 10 ? '0' + new Date().getHours() : new Date().getHours(),
                minutes: new Date().getMinutes() < 10 ? '0 ' + new Date().getMinutes() : new Date().getMinutes(),
            }
        },
        methods: {
            sendMess(){
                const time = `${this.hours} : ${this.minutes}`
                if (this.message.length > 0){
                    const mess = {};
                    mess.body = this.message.trim()
                    mess.sendId = this.sendId
                    mess.time = time
                    this.$emit('new-mess', mess)
                    this.message = ''
                }
            },
            sendMessPhoto(){
                const time = `${this.hours} : ${this.minutes}`
                if (this.photo != ''){
                    const mess = {};
                    mess.body = this.comment.trim()
                    mess.photo = this.photo.trim()
                    mess.sendId = this.sendId
                    mess.time = time
                    this.$emit('newPhoto', mess)
                    this.comment = this.photo = ''
                    this.window = false
                }
            }
        }

    }
</script>